.mlp-translation-metabox {
    &--post .product_data {
        overflow: hidden;

        .wc-tabs {
            margin: 0;
            width: 20%;
            float: left;
            line-height: 1em;
            padding: 0 0 10px;
            position: relative;
            background-color: #fafafa;
            border-right: 1px solid #eee;
            box-sizing: border-box;

            &::after {
                content: '';
                display: block;
                width: 100%;
                height: 9999em;
                position: absolute;
                bottom: -9999em;
                left: 0;
                background-color: #fafafa;
                border-right: 1px solid #eee;
            }

            li {
                display: block;
                font-size: .8125rem;
                margin: 0;
                padding: 0;
                position: relative;

                a {
                    color: #0073aa;
                    margin: 0;
                    padding: 10px;
                    display: block;
                    box-shadow: none;
                    text-decoration: none;
                    line-height: 20px !important;
                    border-bottom: 1px solid #eee;

                    span {
                        margin-left: 0.618em;
                        margin-right: 0.618em;
                    }

                    &::before {
                        @include iconbeforedashicons('\f107');
                    }
                }

                &.mlp-general-settings a::before {
                    content: '\f107';
                }

                &.mlp-inventory-settings a::before {
                    content: '\f481';
                }

                &.mlp-attribute-settings a::before {
                    content: '\f175';
                }

                &.mlp-advanced-settings a::before {
                    content: '\f111';
                }

                &.mlp-variations-settings a::before {
                    content: '\f509';
                }

                &.active a {
                    color: #555;
                    position: relative;
                    background-color: #eee;
                }
            }
        }

        // Form Fields
        .form-field {
            textarea,
            input,
            select {
                font-size: .875rem;
            }
        }


        .woocommerce_options_panel,
        .wc-metaboxes-wrapper {
            float: left;
            width: 80%;
        }
    }
}
